.play {
	border: none;
	border-radius: 4px;
	display: flex;

	&--large {
		cursor: pointer;
		background-color: #576BCC;
		padding: 20px 20px 20px 24px;
	}

	&--small {
		width: 32px;
		height: 32px;
		cursor: inherit;
		padding: 10px 10px 10px 12px;
		background-color: #F1F3FE;
	}
}